import "./css/homepage.css";
import "./css/general.css";
import "./css/gameboard.css";
import "./css/gameover.css";
import "./css/placeShip.css";
import {
  renderHomePage,
  getStartBtn,
  clearContent,
} from "./modules/DOM/homepage";
import Game from "./modules/DOM/gameController";
import renderPage from "./modules/DOM/placeShip";

window.addEventListener("DOMContentLoaded", renderHomePage());

const startBtn = getStartBtn();

startBtn.addEventListener("click", () => {
  const homepage = document.querySelector("#homepage");
  clearContent(homepage);
  renderPage();
  Game()
});
