___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --shipColour: #FFFF00;
    --hitColour: #008000;
    --missColour: #FF0000;
}

@font-face{
    font-family: "robotoBlack";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.cell{
    width: 40px;
    height: 40px;
    border: 1px solid black;
    background-color: #ffffff;
    cursor: pointer;
}

.board_container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}


.row{
    display: flex;
}

.ship{
    background-color: var(--shipColour);
}

.hit{
    background-color: var(--hitColour);
}

.miss{
    background-color: var(--missColour);
}

.player_turn{
    position: absolute;
    top: 250%;
}

.colour{
    width: 25px;
    height: 25px;
    border-radius: 50%;
}

.ship_box > .colour{
    background-color: var(--shipColour);
}

.miss_box > .colour{
    background-color: var(--missColour);
}

.hit_box > .colour{
    background-color: var(--hitColour);
}

.battleship > header{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 7px;
    position: relative;
    color: #ffffff;
}

.board_container, .battleship > header{
    background-color: #2d545e;

}

.ship_box, .hit_box, .miss_box{
    display: flex;
    gap: 12px;
}
.ship_box > span, .hit_box > span, .miss_box > span{
    font-size: 1.4rem;
    font-family: "robotoBlack", sans-serif;
}

.keys_container{
    display: flex;
    gap: 25px;
