  font-family: "robotoSlapBlack";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
  font-family: "robotoSlapThin";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.homepage {
  width: 100%;
  height: 100%;
}

.start_game_btn {
  border: 0;
  border-radius: 8px;
  background-image: linear-gradient(144deg, #af40ff, #5b42f3 50%, #00ddeb);
  box-shadow: rgba(151, 65, 252, 0.2) 0 15px 30px -5px;
  color: #ffffff;
  cursor: pointer;
  max-width: 100%;
  min-width: 140px;
  padding: 19px 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1em;
  font-size: 20px;
  font-family: "robotoSlapBlack";
}

.start_game_btn:active,
.start_game_btn:hover {
  outline: 0;
}

.homepage {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.homepage > header {
  position: absolute;
  top: 15%;
}

.homepage > header > h1 {
  font-size: 4rem;
  font-weight: 700;
  letter-spacing: 1.2rem;
  text-shadow: 1px 1px 20px orange;
  font-family: "robotoSlapThin";
  color: #ffffff;
}
