___CSS_LOADER_EXPORT___.push([module.id, `@font-face {
  font-family: "robotoLight";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.place_ship {
  display: flex;
  justify-content: center;
  align-items: center;
  width: inherit;
  height: inherit;
  background-color: #2d545e;
}

.place_ship button, .input_section button {
  cursor: pointer;
  font-size: 1.2rem;
  font-family: "robotoLight", sans-serif;
  font-weight: 900;
  padding: 3px 12px;
  border-radius: 10px;
  border: 1px solid black;
  outline: none;
}

.ship_section {
  display: grid;
  grid-template-columns: repeat(7, 50px);
  gap: 7px;
}

.main{
    display: flex;
    flex-direction: column;
    gap: 15px;
    position: relative;
}

.ship_section button:first-child {
    grid-column: 1 / span 2;
}
.ship_section button:nth-child(2) {
    grid-column: 3 / span 2;
}
.ship_section button:nth-child(3) {
    grid-column: 5 / span 2;
}

.ship_section button:nth-child(4) {
    grid-row: 2 / span 1;
    grid-column: 2 /span 2;
}

.ship_section button:nth-child(5) {
    grid-row: 2 / span 1;
    grid-column: 4 / span 2;
}

.button_section{
    display: grid;
    grid-template-columns: repeat(7, 50px);
    gap: 7px;
}

.button_section button:first-child{
    grid-column: 1 / span 2;
}

.button_section button:nth-child(2){
    grid-column: 3 / span 2;
}

.button_control{
    display: flex;
    margin-top: 7px;
    gap: 12px;
}

.input_section label{
    display: block;
    font-family: "robotoLight", sans-serif;
    font-weight: 700;
    margin-bottom: 3px;
    color: white;
}

.input_section input{
    padding: 3px 12px;
    border-radius: 5px;
}

.input_section.invisible{
    display: none;
}

.input_section.visible{
    display: block;
}

.input_section{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.error{
    display: block;
    font-family: "robotoLight";
    color: #FF0800;
    font-weight: 900;
    font-size: 1.2rem;
}

.toggle_container {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 170px;
    border: 3px solid #2a52be;
    border-radius: 20px;
    background: #2a52be;
    font-weight: bold;
    color: #2a52be;
    cursor: pointer;
  }
  .toggle_container::before {
    content: "";
    position: absolute;
    width: 50%;
    height: 100%;
    left: 0%;
    border-radius: 20px;
    background: white;
    transition: all 0.3s;
  }
  .checkbox:checked + .toggle_container::before {
    left: 50%;
  }
  .toggle_container div {
    padding: 6px;
    text-align: center;
    z-index: 1;
    font-size: 1.2rem;
    font-family: "robotoLight", sans-serif;
  }
  .checkbox {
    display: none;
  }
  .checkbox:checked + .toggle_container div:first-child {
    color: white;
    transition: color 0.3s;
  }
  .checkbox:checked + .toggle_container div:last-child {
    color: #2a52be;
    transition: color 0.3s;
  }
  .checkbox + .toggle_container div:first-child {
    color: #2a52be;
    transition: color 0.3s;
  }
  .checkbox + .toggle_container div:last-child {
    color: white;
    transition: color 0.3s;
  }`, "",{"version":3,"sources":["webpack://./src/css/placeShip.css"],"names":[],"mappings":"AAAA;EACE,0BAA0B;EAC1B,4CAA8C;AAChD;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,cAAc;EACd,eAAe;EACf,yBAAyB;AAC3B;;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,sCAAsC;EACtC,gBAAgB;EAChB,iBAAiB;EACjB,mBAAmB;EACnB,uBAAuB;EACvB,aAAa;AACf;;AAEA;EACE,aAAa;EACb,sCAAsC;EACtC,QAAQ;AACV;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,kBAAkB;AACtB;;AAEA;IACI,uBAAuB;AAC3B;AACA;IACI,uBAAuB;AAC3B;AACA;IACI,uBAAuB;AAC3B;;AAEA;IACI,oBAAoB;IACpB,sBAAsB;AAC1B;;AAEA;IACI,oBAAoB;IACpB,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,sCAAsC;IACtC,QAAQ;AACZ;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,eAAe;IACf,SAAS;AACb;;AAEA;IACI,cAAc;IACd,sCAAsC;IACtC,gBAAgB;IAChB,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;AACpC;;AAEA;IACI,cAAc;IACd,0BAA0B;IAC1B,cAAc;IACd,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,qCAAqC;IACrC,YAAY;IACZ,yBAAyB;IACzB,mBAAmB;IACnB,mBAAmB;IACnB,iBAAiB;IACjB,cAAc;IACd,eAAe;EACjB;EACA;IACE,WAAW;IACX,kBAAkB;IAClB,UAAU;IACV,YAAY;IACZ,QAAQ;IACR,mBAAmB;IACnB,iBAAiB;IACjB,oBAAoB;EACtB;EACA;IACE,SAAS;EACX;EACA;IACE,YAAY;IACZ,kBAAkB;IAClB,UAAU;IACV,iBAAiB;IACjB,sCAAsC;EACxC;EACA;IACE,aAAa;EACf;EACA;IACE,YAAY;IACZ,sBAAsB;EACxB;EACA;IACE,cAAc;IACd,sBAAsB;EACxB;EACA;IACE,cAAc;IACd,sBAAsB;EACxB;EACA;IACE,YAAY;IACZ,sBAAsB;EACxB","sourcesContent":["@font-face {\r\n  font-family: \"robotoLight\";\r\n  src: url(../fonts/static/RobotoSlab-Light.ttf);\r\n}\r\n\r\n.place_ship {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: inherit;\r\n  height: inherit;\r\n  background-color: #2d545e;\r\n}\r\n\r\n.place_ship button, .input_section button {\r\n  cursor: pointer;\r\n  font-size: 1.2rem;\r\n  font-family: \"robotoLight\", sans-serif;\r\n  font-weight: 900;\r\n  padding: 3px 12px;\r\n  border-radius: 10px;\r\n  border: 1px solid black;\r\