const battleShipWrapper = document.querySelector(".battleship");
const gameContent = document.createElement("main");
const header = document.createElement("header");
const playerTurn = document.createElement("h1");
const keysContainer = document.createElement("div");
const ship = document.createElement("div");
const hit = document.createElement("div");
const miss = document.createElement("div");
const hitKey = document.createElement("span");
const shipKey = document.createElement("span");
const missKey = document.createElement("span");
const hitKeyColour = document.createElement("div");
const missKeyColour = document.createElement("div");
const shipKeyColour = document.createElement("div");

hitKey.textContent = "Hit";
shipKey.textContent = "Ship";
missKey.textContent = "Miss";
playerTurn.textContent = "Player One's Turn";

keysContainer.setAttribute("class","keys_container");
hit.setAttribute("class","hit_box");
miss.setAttribute("class","miss_box");
ship.setAttribute("class","ship_box");
hitKeyColour.setAttribute("class","colour");
shipKeyColour.setAttribute("class","colour");
missKeyColour.setAttribute("class","colour");
gameContent.setAttribute("class", "board_container");
playerTurn.setAttribute("class", "player_turn");
header.setAttribute("class", "game_header");


keysContainer.appendChild(ship);
keysContainer.appendChild(hit);
keysContainer.appendChild(miss);
ship.appendChild(shipKey);
ship.appendChild(shipKeyColour);
hit.appendChild(hitKey);
hit.appendChild(hitKeyColour);
miss.appendChild(missKey);
miss.appendChild(missKeyColour);
header.appendChild(keysContainer);
header.appendChild(playerTurn);

export const createGameBoard = (player, id) => {
  const board = player.gameboard.gameboard;
  const gameboard = document.createElement("div");
  gameboard.setAttribute("class", "gameboard");
  gameboard.setAttribute("id", id);
  for (let row = 0; row < 10; row++) {
    const rowElement = document.createElement("div");
    rowElement.setAttribute("data-id", row);
    rowElement.setAttribute("class", "row");
    for (let col = 0; col < 10; col++) {
      const colElement = document.createElement("div");
      colElement.setAttribute("class", "cell");
      colElement.setAttribute("data-id", col);
      if (player.type === "Human" && board[row][col]) colElement.classList.add("ship");
      rowElement.appendChild(colElement);
      gameboard.appendChild(rowElement);
    }
  }
  return gameboard;
};

export const renderPlayerBoard = (gameboard) => {
  gameContent.appendChild(gameboard);
  battleShipWrapper.appendChild(header)
  battleShipWrapper.appendChild(gameContent);
};
