export let shipLength = null;

shipSection.addEventListener("click", (event) => {
  if (event.target.tagName === "BUTTON") {
    displayInput();
    shipLength = event.target.dataset.length;
  }
});

inputCancelButton.addEventListener("click", () => {
    const input = document.getElementById("coords");
    clearContent(background);
    input.value = "";
    showErrorMessage("")
})


export default function renderPage() {
  battleShipWrapper.appendChild(placeShipWrapper);
}

const displayInput = () => {
  battleShipWrapper.appendChild(background);
  inputSection.classList.add("visible");
  ("");
};

function createGameBoard(player) {
  const gameboard = document.createElement("div");
  gameboard.setAttribute("class", "gameboard");
  gameboard.setAttribute("id", player);
  for (let row = 0; row < 10; row++) {
    const rowElement = document.createElement("div");
    rowElement.setAttribute("data-id", row);
    rowElement.setAttribute("class", "row");
    for (let col = 0; col < 10; col++) {
      const colElement = document.createElement("div");
      colElement.setAttribute("class", "cell");
      colElement.setAttribute("data-id", col);
      rowElement.appendChild(colElement);
      gameboard.appendChild(rowElement);
    }
  }
  return gameboard;
}

const showErrorMessage = (message) =>{
    error.textContent = message;
}

export const placeShipManually = (player, length, coords, direction = "horizontal") => {
    const numberLength = Number(length);
    const ship = player.gameboard.callShip(numberLength);
    if (!player.gameboard.placeShip(ship, coords, direction)) return false;
    renderShips(player)
    return true;
};

const renderShips = (player) => {
  for (const coord of player.gameboard.coords) {
    const row = document.querySelector(`.row[data-id="${coord[0]}"]`);
    const col = row.querySelector(`.cell[data-id="${coord[1]}"]`);
    col.classList.add("ship");
  }
}