    font-size: 4.5rem;
    font-family: "robotoLight", 'Times New Roman', Times, serif;
}

.new_game{
    padding: 12px 32px;
    font-size: 1.4rem;
    font-family: "robotoLight", 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    cursor: pointer;
}

