import Player from "../Classes/PLayer/PLayer";
import { clearContent } from "./homepage";
import { createGameBoard, renderPlayerBoard } from "./gameboardDOM";
import displayWinner, { playNewGamebtn } from "./gameover";
import renderPage, {
  randomizeButton,
  confirmButton,
  inputConfirmButton,
  placeShipManually,
  shipLength,
  checkbox,
} from "./placeShip";

export default function Game() {
  let playerOne = new Player("Player_one", "Human");
  let playerTwo = new Player("Player_two", "Computer");
  let activePlayer = playerOne;
  let enemyPlayer = playerTwo;
  let gameOver = false;
  let isRandom = false;

  const switchRandomMode = (value) => (isRandom = value);

  const switchActivePlayer = () => {
    return (activePlayer = activePlayer === playerOne ? playerTwo : playerOne);
  };

  const switchEnemy = () => {
    return (enemyPlayer = enemyPlayer === playerTwo ? playerOne : playerTwo);
  };

  const displayPlayerTurn = (message) => {
    const playerTurn = document.querySelector(".player_turn");
    playerTurn.textContent = message;
  };

  const checkForWin = (enemy) => {
    if (enemy.gameboard.checkAllShipsSunk()) return (gameOver = true);
  };

  const attackEnemy = (enemy) => {
    if (activePlayer.name !== enemy.name) {
      const enemyGrid = document.getElementById(enemy.name);
      enemyGrid.addEventListener("click", (e) => {
        if (!gameOver) {
          if (e.target.classList.contains("cell")) {
            const row = Number(e.target.parentNode.dataset.id);
            const col = Number(e.target.dataset.id);
            if (
              e.target.classList.contains("miss") ||
              e.target.classList.contains("hit")
            )
              return;
            const attack = enemy.gameboard.receiveAttack([row, col]);
            if (attack) {
              e.target.classList.add("hit");
              enemy.gameboard.gameboard[row][col].isSunk();
              if (checkForWin(enemy)) {
                displayWinner("Congratulations You Win!");
                return;
              }
            } else {
              e.target.classList.add("miss");
              switchActivePlayer();
              switchEnemy();
              displayPlayerTurn("Player Two's Turn");
              setTimeout(computerAttack, 1000, enemyPlayer);
            }
          }
        }
      });
    }
  };

  const computerAttack = (enemy) => {
    const enemyGrid = document.getElementById(enemy.name);
    let row = Math.floor(Math.random() * 10);
    let col = Math.floor(Math.random() * 10);
    let rowElement = enemyGrid.querySelector('.row[data-id="' + row + '"]');
    let colElement = rowElement.querySelector('.cell[data-id="' + col + '"]');
    if (
      colElement.classList.contains("miss") ||
      colElement.classList.contains("hit")
    ) {
      row = Math.floor(Math.random() * 10);
      col = Math.floor(Math.random() * 10);
      rowElement = enemyGrid.querySelector('.row[data-id="' + row + '"]');
      colElement = rowElement.querySelector('.cell[data-id="' + col + '"]');
    }
    let attack = enemy.gameboard.receiveAttack([row, col]);
    while (attack) {
      colElement.classList.add("hit");
      enemy.gameboard.gameboard[row][col].isSunk();
      if (checkForWin(enemy)) {
        displayWinner("Sorry  You lose!");
        return;
      }
      row = Math.floor(Math.random() * 10);
      col = Math.floor(Math.random() * 10);
      rowElement = enemyGrid.querySelector('.row[data-id="' + row + '"]');
      colElement = rowElement.querySelector('.cell[data-id="' + col + '"]');
      attack = enemy.gameboard.receiveAttack([row, col]);
    }
    colElement.classList.add("miss");
    switchActivePlayer();
    switchEnemy();
    displayPlayerTurn("Player One's Turn");
  };

  const init = () => {
    if (isRandom) {
      playerOne.gameboard.randomizePlacements();
    }
    playerTwo.gameboard.randomizePlacements();
    renderPlayerBoard(createGameBoard(playerOne, playerOne.name));
    renderPlayerBoard(createGameBoard(playerTwo, playerTwo.name));
    attackEnemy(enemyPlayer);
  };

  const resetGame = () => {
    playerOne = new Player("Player_one", "Human");
    playerTwo = new Player("Player_two", "Computer");
    activePlayer = playerOne;
    enemyPlayer = playerTwo;
    switchRandomMode(false);
    gameOver = false;
  };

  randomizeButton.addEventListener("click", () => {
    const placeShipWrapper = document.querySelector(".place_ship");
    clearContent(placeShipWrapper);
    if (!isRandom) {
      switchRandomMode(true);
    }
    init();
  });

  inputConfirmButton.addEventListener("click", () => {
    const background = document.querySelector(".background");
    const input = document.getElementById("coords");
    const coords = [...input.value.replaceAll(" ", "")];
    const [row, , col] = coords;
    clearContent(background);
    if (!checkbox.checked) {
      placeShipManually(playerOne, shipLength, [Number(row), Number(col)]);
    } else {
      placeShipManually(
        playerOne,
        shipLength,
        [Number(row), Number(col)],
        "vertical"
      );
    }
  });

  confirmButton.addEventListener("click", () => {
    if (playerOne.gameboard.ships.length < 5) return;
    const placeShipWrapper = document.querySelector(".place_ship");
    clearContent(placeShipWrapper);
    if (isRandom) {
      switchRandomMode(false);
    }
    init();
  });

  playNewGamebtn.addEventListener("click", () => {
    const header = document.querySelector(".game_header");
    const background = document.querySelector(".background");
    const boardContainer = document.querySelector(".board_container");
    boardContainer.replaceChildren();
    clearContent(boardContainer);
    clearContent(header);
    clearContent(background);
    renderPage();
    resetGame();
  });

  return { init };
}
